<template>
  <div class="farm flex flex-column page container-full">
    <header>
      <h1 class="text">{{ language.menu_3 }}</h1>
      <p>{{ language.tip_4_1 }}<br />{{ language.tip_4_2 }}</p>

      <div class="obtion">
        <div
          class="obtion-left"
          :class="statusStype ? 'left-back1' : 'left-back'"
          @click="getFn(false)"
        >
          {{language.get}}DFT
        </div>
        <div
          class="obtion-right"
          :class="statusStype ? 'left-back' : 'left-back1'"
          @click="getFn(true)"
        >
            {{language.get}}Dcoin
        </div>

        <label class="search-box">
          <input
            type="text"
            v-model="searchText"
            :placeholder="language.tip_4_3"
          />
        </label>
      </div>

      <div class="filter-bar flex jc-end ai-center">
        <m-switch v-if="false" v-model="filterFlag"
          ><span class="text">{{ language.tip_4_4 }}</span></m-switch
        >
        <div class="flex">
          <!--          <div class="sort-box flex jc-between ai-center text">-->
          <!--            <span>{{ language.text_3_2 }}</span>-->
          <!--            <img class="icon-s" src="../images/option/order1.png" alt="">-->
          <!--          </div>-->
        </div>
      </div>
    </header>
    <div class="farm-container flex1 flex flex-wrap ai-start">
      <template v-for="pid in poolLength" :key="'pool-' + pid">
        <farm-round
          class="farm-item"
          :pid="pid - 1"
          :filter-string="searchText"
          v-if="!statusStype"
        />
        <farm-dcoin
          class="farm-item"
          :pid="pid - 1"
          :filter-string="searchText"
          v-if="statusStype"
        />
      </template>

      <div v-if="loading" class="mask flex jc-center ai-center">
        <i class="icon-loading"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import MSwitch from "@/components/mSwitch";
import FarmRound from "@/views/farm/farmRound";
import farmDcoin from "@/views/farm/farmDcoin";

export default {
  name: "farm",
  components: { FarmRound, MSwitch, farmDcoin },
  computed: {
    ...mapGetters(["language"]),
  },
  data() {
    return {
      statusStype: false,
      filterFlag: false,
      searchText: "",
      poolLength: 0,
      loading: false,
      LPPool: "LPPool",
      LPPoolDcoin: "DCoinLPPool",
    };
  },
  methods: {
    ...mapActions("farm", ["setFarmTable"]),
    getPoolList() {
      this.loading = true;
      this.$api
        .getPoolLength(this.LPPool)
        .then((length) => {
          this.poolLength = Number(length);
          setTimeout(() => (this.loading = false), 500);
        })
        .catch(() => (this.loading = false));
    },
    // 获取dcoin数据
    getdcoinList() {
      this.loading = true;
      this.$api
        .getPoolLength(this.LPPoolDcoin)
        .then((length) => {
          this.poolLength = Number(length);
          setTimeout(() => (this.loading = false), 500);
        })
        .catch(() => (this.loading = false));
    },
    getFn(type) {
      this.statusStype = type;
      this.setFarmTable(type);
      if (type) {
        this.getdcoinList();
      } else {
        this.getPoolList();
      }
    },
  },
  beforeMount() {
    this.getPoolList();
  },
};
</script>

