<template>
  <div class="farm-round" v-if="isFiltered">
    <header class="flex ai-center">
      <div class="pair-box">
        <img class="icon b1" :src="coinMap[info.token0symbol]" alt="">
        <img class="icon b2" :src="coinMap[info.token1symbol]" alt="">
      </div>
      <p class="flex flex-column ai-start">
        <span class="text fs-16">{{ info.farmPair }} LP</span>
        <span class="link-btn flex ai-center green" @click="toPoolLink(info.lpToken)">
          {{ language.tip_4_8 }}
          <img class="icon-xs" src="../../images/farm/share.png" alt="">
        </span>
      </p>
    </header>
    <div class="statistics flex jc-between">
      <div>
        <p class="label relative">{{ language.tip_4_9 }}
          <!--          <img class="icon-xs" src="../../images/farm/calculation.png" alt="">-->
          <!--          <m-tip class="tip-box absolute" direction="left"></m-tip>-->
        </p>
        <p class="green">{{ annual_rate }} %</p>
      </div>
      <div>
        <p class="label relative">{{ language.tip_4_10 }}
          <img class="icon-xs" src="../../images/farm/help.png" alt="">
          <m-tip class="tip-box absolute">{{ language.tip_4_15_dcoin}}</m-tip>
        </p>
        <p class="text">{{ info.allocPoint || '——' }}x</p>
      </div>
      <div>
        <p class="label relative jc-end">{{ language.tip_4_11 }}
          <img class="icon-xs" src="../../images/farm/help.png" alt="">
          <m-tip class="tip-box absolute" direction="right">{{ language.tip_4_16 }}</m-tip>
        </p>
        <p class="text">{{ mobility }}</p>
      </div>
    </div>
    
    <div class="farm-bar bg-radius">
      <p class="flex jc-between ai-center">
        <span class="text">Dcoin  {{ language.tip_4_12 }}</span>
      </p>
      <p class="flex jc-between ai-center">
        <span class="green fs-18">{{ all_profit }}</span>
        <m-button v-if="enabled" class="btn border-green hdr-el-green green" @click="reap" :loading="reapLoading"
                  :height="36">
          {{ language.btn_reap }}
        </m-button>
        <m-button v-else class="btn border-gray hdr-el-gray gray" :disabled="true" :height="36">
          {{ language.btn_reap }}
        </m-button>
      </p>
    </div>



    <div v-if="enabled" class="farm-bar bg-radius">
      <p class="flex jc-between ai-center">
        <span class="text">{{ info.farmPair }} LP {{ language.tip_4_13 }}</span>
        <!--        加仓层显示按钮-->
        <m-button style="font-size:30px" class="btn border-green hdr-el-green green" @click="showPopper(1);" :height="36">
          +
        </m-button>
      </p>
      <p class="flex jc-between ai-center">
        <span class="red fs-18">{{ betAmount.myFixed() }}</span>
        <!--        减仓层显示按钮-->
        <m-button style="font-size:30px" class="btn border-red hdr-el-red red" @click="showPopper(0);" :height="36">
          -
        </m-button>
      </p>
    </div>


    <!-- 启用农场 -->
    <div v-else class="farm-bar">
      <p class="flex jc-center ai-center">
        <m-button class="btn-l border-green hdr-el-green green"
                  @click="approveFarm(info.lpToken)" :loading="enable_loading"
                  :height="36"
        >
          {{ language.btn_enable }}
        </m-button>
      </p>
    </div>


    <p class="flex jc-center ai-center">
        <span class="link-btn btn-get flex ai-center green" @click="getLp">
          {{ language.get }} {{ info.farmPair }} LP
          <img class="icon-xs" src="../../images/farm/share.png" alt="">
        </span>
    </p>




    <!--  加/减仓弹层-->
    <m-popper v-model="betDialogShow">
      <template v-slot:title>{{ language[actionType ? 'btn_put_in' : 'btn_take_out'] }}</template>
      <div class="inner">
        <div class="input-bar">
          <p class="flex jc-between ai-center">
            <span class="text">{{ info.farmPair }} LP</span>
            <span>{{ language.available }}：<span class="text">{{ maxAmount.myFixed() }}</span></span>
          </p>
          <label class="flex jc-between ai-center">
            <input class="flex1" type="number" v-model="amountDisplay" @input="amountInput" :placeholder="language.tip_2_3">
            <span class="max-btn green" @click="setAmountMax()">MAX</span>
          </label>
        </div>
        <p class="tip text">
          {{
            language.tip_notice + language.tip_4_14.replace('{action}', language[actionType ? 'btn_put_in' : 'btn_take_out'])
          }}
        </p>
        <div class="flex jc-center ai-center">
          <m-button v-if="buyAmount.gt(0)" class="btn hdr-el-blue" @click="actionHandle()" :loading="actionLoading"
                    :height="36">
            {{ language[actionType ? 'btn_put_in' : 'btn_take_out'] }}
          </m-button>
          <m-button v-else class="btn border-gray hdr-el-gray gray" :disabled="true" :height="36">
            {{ language[actionType ? 'btn_put_in' : 'btn_take_out'] }}
          </m-button>
        </div>
      </div>
    </m-popper>


  </div>
</template>

<script>
import MButton from "@/components/mButton";
import {mapActions, mapGetters, mapState} from "vuex";
import {poolInfoConvertor,inputNumberFixer} from "@/common/dataConvertor";
import {BN} from "@/common/util";
import {blockConfig, coinMap} from "@/common/walletConfig";
import MPopper from "@/components/mPopper";
import MTip from "@/components/mTip";

export default {
  name: "farmRound",
  components: {MTip, MPopper, MButton},
  props: {
    pid: {type: Number, require: true},
    filterString: {type: String, default: ''}
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address']),
    buyAmount() {
      let {amountDisplay} = this;
      return amountDisplay ? new BN(amountDisplay).toWei('balance') : new BN(0);
    },
    maxAmount(){
      return this.actionType ? this.LPBalance : this.betAmount;
    },
    isFiltered() {
      return this.info.farmPair.indexOf(this.filterString.toUpperCase()) !== -1;
    }
  },
   
  data() {
    return {
      coinMap,
      info: {
        lpToken: '',//合约地址
        allocPoint: '', //占比例
        lastRewardBlock: '',//最后收益区块
        accDetTokenPerShare: '', //将池因子
        name: '', //名称
        status: '', //状态 1：启用 0：禁用
        token0: '', //token0地址
        token1: '',//token1地址
        token0symbol: '',//token0 symbol
        token1symbol: '',//token1 symbol
        farmPair: '',
        token0decimals: '', //token0精度
        token1decimals: '', //token1精度
        lpTotalInQuoteToken: '' //lpToken总价值是token1的倍数
      },
      mobility: '——',
      annual_rate: '——',
      all_profit: '0',
      betAmount: new BN(0),
      enabled: false, //农场是否启用
      enable_loading: false,
      timer: null,
      betDialogShow: false,
      amountDisplay: '',
      LPBalance: new BN(0),
      actionType: 0,
      reapLoading: false,
      actionLoading: false,
      tip_bg_color: '#353b4d',
     LPPoolDcoin:"DCoinLPPool",
      
    }
  },
  methods: {
    ...mapActions('farm', ['setFarmInfo']),
    // 查看合约地址
    toPoolLink(lpToken) {
      window.open(blockConfig.poolHost + lpToken);
    },
    getPoolInfo(isFirstRequest) {
      return this.$api.getPoolInfo(this.pid,this.LPPoolDcoin).then(info => {
        this.info = poolInfoConvertor(info);
        this.setFarmInfo({pid: this.pid, info: this.info});
        this.getFarmState(info.lpToken);
        this.getProfitRate(info);
        isFirstRequest && this.getPoolBalance(info.lpToken);
      })
    },
    getProfitRate(pool) {
      this.$api.getProfitRate(this.pid, pool,this.LPPoolDcoin).then((res) => {
        this.annual_rate = new BN(res.annual_rate).toRate();
        this.mobility = new BN(res.mobility).toFixed(2);
      })
    },
    getUserInfoInFarm() {
      let {pid, address} = this;
      address && this.$api.getUserInfoInFarm(pid, address,this.LPPoolDcoin).then(({betAmount, all_profit, rewardAmount}) => {
        betAmount = new BN(betAmount).toDisplay('balance');
        all_profit !== '0' && (all_profit = new BN(all_profit).toDisplay('balance').myFixed());
        rewardAmount !== '0' && (rewardAmount = new BN(rewardAmount).toDisplay('balance').myFixed());
        this.betAmount = betAmount;
        this.all_profit = all_profit;
        this.rewardAmount = rewardAmount;
        this.setFarmInfo({pid, info: {betAmount, all_profit, rewardAmount}});
      })
    },
    getFarmState(farm_address) {
      let {address} = this;
      address && this.$api.getFarmState(address, farm_address,this.LPPoolDcoin).then(res => {
        this.enabled = res !== '0';
      })
    },
    // 启用农场
    approveFarm(farm_address) {
      if (!this.address) return;
      if (!farm_address) return;
      this.enable_loading = true;
      this.$api.approveFarm(farm_address,this.LPPoolDcoin).then(() => {
        this.enabled = true;
        this.enable_loading = false;
      }).catch(() => {
        this.enable_loading = false;
      });
    },
    //  actionType == 0加仓    actionType == 1减仓  
    showPopper(actionType){
      this.betDialogShow = true;
      this.actionType = actionType;
      this.amountDisplay = '';
    },
    // 减仓加仓input方法
    amountInput(e) {
 
      let val = e.target.value;
      let max = this.maxAmount;
      val = Number(val) > max ? max.toString(10) : val;
      val = inputNumberFixer(val);
      this.amountDisplay = val;
    },
    getPoolBalance(farm_address) {
      this.address && this.$api.getPoolBalance(this.address, farm_address).then(balance => {
        this.LPBalance = new BN(balance).toDisplay('balance')
      })
    },
    // max大值赋值
    setAmountMax(){
      this.amountDisplay = inputNumberFixer(this.maxAmount.toString(10));
    },
    // 加仓 减仓
    actionHandle() {
      let {pid, buyAmount, $api, actionType,LPPoolDcoin} = this;
      buyAmount = buyAmount.toString(10);
      let promise = actionType ? $api.deposit(pid, buyAmount,LPPoolDcoin) : $api.withdraw(pid, buyAmount,LPPoolDcoin);
      this.actionLoading = true;
      promise.on('transactionHash', () => {
        this.actionLoading = false;
        this.betDialogShow = false;
      }).catch(() => {
        this.actionLoading = false;
      })
    },
    // 收割
    reap() {
      this.reapLoading = true;
      this.$api.withdraw(this.pid, 0,this.LPPoolDcoin).then(() => {
        this.reapLoading = false;
        this.reapLoading = false;
      }).catch(() => {
        this.reapLoading = false;
      })
    },
    // 点击币的链接地址
    getLp() {
      let {token0, token1} = this.info;
      window.open(blockConfig.getLpHost + token0 + '/' + token1)
    },
  },
  beforeMount() {
    this.getPoolInfo(true);
    this.getUserInfoInFarm();
    clearInterval(this.timer);
    this.timer = setInterval(() => {
      this.getPoolInfo();
      this.getUserInfoInFarm();
    }, 5000);
  },
  beforeUnmount(){
        clearInterval(this.timer);
        this.timer = null;
  }
}
</script>

