<template>
  <div v-if="p_value" class="m-popper flex jc-center ai-center"
       @click.self="hide"
       :class="lifeClass">
    <div class="popper-box">
      <div class="frame">
        <header class="flex jc-between ai-center">
          <span class="text"><slot name="title">Title</slot></span>
          <img class="btn-close" src="../images/close@2x.png" @click="hide" alt=""/>
        </header>
        <div class="p-content">
          <slot></slot>
        </div>
        <footer></footer>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mPopper",
  props: {
    modelValue: {type: Boolean, require: true}
  },
  emits: ['update:modelValue'],
  data() {
    return {
      p_value:false,
      lifeClass: 'hide',
    }
  },
  watch: {
    modelValue(bool) {
      bool?this.show():this.hide();
    }
  },
  methods: {
    setLifeClass(bool) {
      this.lifeClass = bool ? 'show' : 'hide';
    },
    show(){
      this.p_value = true;
      this.$emit('update:modelValue', true);
      setTimeout(() => this.setLifeClass(true), 20);
    },
    hide() {
      this.setLifeClass(false);
      setTimeout(() => {
        this.p_value = false;
        this.$emit('update:modelValue', false);
      }, 300);
    },
  },
  mounted() {
    this.setLifeClass(this.modelValue);
  }
}
</script>

