  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .6);
  z-index: 999;
  opacity: 0;
  transition: all ease .3s;
  &.show {
    opacity: 1;
  }
  &.hide {
    opacity: 0;
  }
}
