<template>
  <span class="m-switch flex ai-center">
    <span class="m-switch-box flex" style="display: inline-block; float: left;" @click="p_value=!p_value;exerciseFn()" :class="{checked:modelValue}">
      <i class="animate flex"></i>
    </span>
     <slot></slot>
   
        
  </span>
</template>

<script>
export default {
  name: "mSwitch",
  props: {
    modelValue: {type: Boolean, default: false}
  },
  emits: ['update:modelValue'],
  computed: {
    p_value: {
      get() {
        return this.modelValue;
      },
      set(bool) {
        this.$emit('update:modelValue', bool);
      }
    }
  },
  data() {
    return {}
  },
  methods:{
    exerciseFn(){
        this.$emit('exerciseFn', this.p_value);
    }
      
  }
}
</script>

<style lang="less" scoped>
.m-switch{
  cursor: default;

 
   div{
    display: flex;
   }
}
.m-switch-box {
  
  width: 45px;
  height: 26px;
  position: relative;
  background-color: #353B4D;
  border-radius: 13px;
  cursor: pointer;
  margin-right: 15px;
  i {
    display: block;
    width: 22px;
    height: 22px;
    background-color: #FFFFFF;
    border-radius: 50%;
    position: absolute;
    top: 2px;
    left: 2px;
  }
  &.checked {
    background-color: #2BEC93;
    i {
      left: 21px;
    }
  }
}
</style>